.form-select-program__cat-and-program-wrapper {
  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
  }
}

.form-select-program__fieldset {
  padding: 0;
  margin: 0;
  margin-bottom: 45px;
  border: none;

  &--with-title {
    margin-bottom: 24px;
  }
}

.form-select-program__list {
  padding: 0;
  margin: 0;
  list-style-type: none;

  &--inline {
    @media (min-width: @desktop-width) {
      display: flex;
      justify-content: space-between;
    }
  }

  &--additional {
    @media (min-width: @tablet-width) {
      display: grid;
      grid-template-columns: 225px 225px;
    }

    @media (min-width: @desktop-width) {
      display: flex;
      justify-content: space-between;
    }
  }
}

.form-select-program__item {
  font-family: @oswald;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: @specialgrey;

  &:not(:last-child) {
    padding-bottom: 14px;
  }

  &--to-write input {
    padding: 14px;
  }
}

.form-select-program__legend {
  margin: 0;
  padding: 0;
  padding-bottom: 35px;
  font-family: @oswald;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
}

.form-select-program__textfield {
  display: flex;
  flex-direction: column;
  margin-bottom: 23px;
}

.form-select-program__textarea {
  padding: 14px;
  padding-bottom: 118px;
}

.form-select-program__required {
  font-family: @lato;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
}

.form-select-program__button-wrapper {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 285px 285px;
    column-gap: 60px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 570px 570px;
    column-gap: 80px;
  }
}

.form-select-program__button {
  padding: 10px 78px;
  margin-bottom: 21px;
}
